.userProfile .cover-photos{
    height: 200px;
    aspect-ratio: 1/1;
    width: 100%;
    overflow: hidden;
    margin-top: 3.6rem;
 
}
.profile-info{
    width: 90%;
    display: flex;
    align-items: center;
    flex-direction: column;
    gap: .7rem;
    background-color: var(--color-card);
    padding: 20px;
    border-radius: 1rem;
    text-align: center;
    position: relative;
    margin:1rem auto;
    margin-top: -100px;
}
.profile-info>img{
    border-radius: 50%;
    aspect-ratio: 1/1;
    width: 120px;
    margin-top: -80px;
    border: 4px solid var(--color-primary);
}
.profile-button{
    display: flex;
    align-items: center;
    gap: 1rem;
}