.Message{
    background: var(--color-card);
    margin: 20px auto;
    border-radius: 1rem;
    padding: 14px;
}
.message-top{
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-bottom: 1rem;
}
.message-search{
    background: var(--color-bg);
    display: flex;
    gap: 5px ;
    align-items: center;
    border-radius: 1rem;
    padding: .7rem;
    margin-bottom: 1rem;
}
.message-search input{
   background: transparent;
   color: var(--color-text);
  
}
.border-div{
    width: 100%;
    height: .1rem;
    background-color: var(--color-bg);
    margin-bottom: 20px;
}
.message{
    display: flex;
    margin-bottom: 1rem;
    gap: 5px;

}
.message .user{
    position: relative;
    height: fit-content;
    width: fit-content;
}
.green-active{
    height: 10px;
    width: 10px;
    background-color:var(--color-primary);
    position:absolute;
    bottom: 0;
    right: 0;
    border-radius: 50%;
}
.message p{
 font-size: .9rem;
}