.comments{
    width: 100%;
    background-color: var(--color-bg);
    padding: 1rem;
    border-radius: .5rem;
    display: flex;
    flex-direction: column;
    gap: 1rem;
    margin: 1rem 0;
}
.comments .write-box{
    width: 100%;
    justify-content: space-between;
}
.comments .write-box input{
    width: 100%;
    background-color: transparent;
    border: 1px solid var(--color-soft);
    padding: 10px;
    border-radius: .5rem;
    color: var(--color-text);
}